import React from "react";
import {HexFile, HexFileService} from "services/hexFileService";
import {Button, Col, Container, Row} from "react-bootstrap";

const HexFileComponent = () => {
  const [hexFiles, setHexFiles] = React.useState<Array<HexFile>>([]);

  const init = () =>{
    var service = new HexFileService();
    service.listHexFilesToDownload()
      .then(setHexFiles);
  };

  const downloadFile = (hexFile: HexFile) => {
    fetch(hexFile.downloadUrl, {method: 'GET'})
      .then((response) => response.blob())
      .then((blob) => {
        const url = window.URL.createObjectURL(new Blob([blob]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download',hexFile.name);
        document.body.appendChild(link);
        link.click();
        if(link.parentNode === null)
          throw Error('not possible');
        link.parentNode.removeChild(link);
      });
  };

  const mapHexFilesToComponent = (value: HexFile, index: number): any => {
    return (
      <Row key={index}>
        <Col><a href={value.downloadUrl}>{value.name}</a></Col>
        <Col><Button variant={"outline-dark"} onClick={() => downloadFile(value)}>Download</Button></Col>
      </Row>);
  };

  React.useEffect(init, []);
  return (
    <Container>
      <h1>Hex Files</h1>
      {hexFiles && hexFiles.map(mapHexFilesToComponent)}
    </Container>
  )
};

export default HexFileComponent;
